@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


.single-outer{
    width: 100%;
    background-color:rgb(231, 231, 231) ;
}
.single-inner{
    width: 90%;
    margin-left: 5%;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 10px 17px 2px rgba(105, 105, 105, 0.2);
    padding-top: 16vh;
    padding-bottom: 5vh;
}
.single-inner-upper{
    display: flex;
    height: 105vh;
}
hr{
    color: gray;
}
.single-report{
    color: rgb(0, 0, 196);
    padding-bottom: 3vh;
    margin-left: 50%;
}

.single-pic{
    height: 57vh;
    box-sizing: content-box;
    width: 41.8%;
    padding: 40px 10px 0 80px;
    border-color: rgb(231, 231, 231);
}
.sin-heart{
    float:right;
    color: lightgray;
    font-size:x-large;
    border: none;
    margin-top: -97%;
    padding-top: -15%;
    background: none;
    z-index:2;
    cursor: pointer;
}
.sin-heart:hover{
    color: rgb(250, 89, 89);
    transition: 0.5s;
}
.single-picture{
    width: 85%;
    height: 430px;
    object-fit: contain;
} 

.sing-img{
    width: 100%;
    height: 54vh;
    object-fit: contain;
    padding: 15px 20px;
    border: 1px solid #f0f0f0;
}
.single-det{
    height: 70vh;
    width: 45%;
    margin-left: 1%;
    margin-top: 5vh;
}
.cart-buy-div{
    display: flex;
    width: 100%;
    height: 20%;
}
.butto-div{
    color: #fff;
    
}
.si-butt-1{
    width: 90%;
    height: 45px;
    border-radius: 2px;
    margin-left: 0.1%;
    margin-top: -1.5%;
    padding-top: 1.2vh;
    display: flex;
    cursor: pointer;
    text-transform: normal;
    text-transform: uppercase;
    font-size:medium;
    font-weight: 600;
    position: relative;
    background-color: rgb(23, 9, 64);
    color: white;
    justify-content: center;
    border: none;
    font-family: "Poppins", sans-serif;
}
.si-butt-1:hover{
    color: #878787;
    transition: 0.5s;
}

.si-butt-2{
    width: 90%;
    height: 45px;
    border-radius: 2px;
    margin-left: 1%;
    padding-top: 1.5vh;
    margin-top: -1.5%;
    display: flex;
    cursor: pointer;
    text-transform: normal;
    text-transform: uppercase;
    font-size:medium;
    font-weight: 600;
    position: relative;
    background-color: rgb(94, 131, 254);
    color: white;
    justify-content: center;
    border: none;
    font-family: "Poppins", sans-serif;
}
.si-butt-2:hover{
    color: black;
    transition: 0.8s;
}
.single-icon{
    margin-top: 0.1vh;
}
.single-rate{
    height: 2.5vh;
    width: 7%;
    background-color: rgb(2, 98, 2);
    padding-top: 0.2%;
    margin-top: 1.5%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    display: flex;
  }
.single-rate p{
    color: #fff;
    margin-left: 15%;
    margin-right: 5%;
    font-weight: 700;
  }
.ra-tex{
    margin-top: 5;
    color: #878787;
    font-size: 14;
}
.sin-para{
    font-weight: 600;
    font-size: 17px;
    color: rgb(165, 165, 165);
}
.sin-desc{
    color: rgb(91, 88, 85); 
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size:x-large
}
.sin-span1-1{
    font-weight:500;
    color: black;
    font-size: 170%;
    margin-top: 1%;
}
.sin-span2-2{
    color: gray;
    padding-left: 10px;
    text-decoration: line-through;
    margin-top: 3%;
    font-size: medium;
}
.sin-rate{
    display:flex;
}
.sin-span2-3{
    color: rgb(2, 98, 2);
    padding-left: 7px;
    font-size: 14px;
    font-weight: 600;
    margin-top: 3%;
    font-size: medium;
}
.rati-box{
    height: 3.5vh;
    width: 10%;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: rgb(2, 98, 2);
    color: white;
    font-family: "Poppins", sans-serif;
    margin-left: 1%;
    margin-top: 1%;
    font-size: small;
}
.rating-sec{
    display: flex;
}
.ra-tex{
    font-size: medium;
    margin-left: 3%;
    margin-top: 1.5%;
    font-family: "Poppins", sans-serif;
}
.sin-avai{
    margin-top: 3.5%;
    font-size: medium;
    font-family: "Roboto", sans-serif;
}
.ty-1{
    margin-top: 10px;
    font-size: 14px;
    vertical-align: baseline;
}
.Badge{
    margin-right: 10px;
    color: #00cc00;
    font-size: 15px;
}
.cart-icon-p{
    font-size: 15px;
    margin-top: 1%;
    margin-left: 2%;
}
.link-cart-div{
    width: 300px;
    height: 100px;
}
.link-buynow-div{
    width: 300px;
}

.single-inner-lower{
    display: inline-flex;
    width: 100%;
    overflow: hidden;
}
.similar-products-container{
    display: inline-flex;
    height: 30vh;
    width: 100%;
    box-shadow: -5px 10px 17px 10px rgba(105, 105, 105, 0.2);
    margin-top: 5vh;
    /* margin-left: 1%;
    margin-right: 1%; */
}
.single-outer1{
    height: 34vh;
    width: 30%;
    margin-left: 2%;
}
.single-img{
    height: 18vh;
    width: 96%;
}
.single-Slider_div2{
    height: 80%;
    width: 98%;
    padding-left: 1%;
    padding-right: 1%;
    /* display: flex; */
}
.single-product-pic img{
    margin-top: 2vh;
    height: 35vh;
    width: 98%;
}
.sugg-full-slider-div{
    height: 45vh;
    width: 96%;
    /* background-color: navy; */
    margin-top: 5%;
    margin-left: 2%;
    margin-right: 10%;
    margin-bottom: 5%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.up_info_div{
    height: 10%;
    width: 80%;
    color: rgb(47, 46, 46);
    display: inline-block;
    font-weight: 300;
    font-family: 'Poppins', sans-serif;
    font-size:x-large;
    margin-left: 1%;
    margin-top: 1%;
}

@media screen and (max-width: 420px) {
    .single-inner-upper{
        flex-direction: column;
        height: 157vh;
    }
    .single-inner {
        width: 96%;
        padding-top: 20%;
        height: 210vh; 
        margin-left: 2%;
    }
    .single-pic {
        height: 20vh;
        width: 70%;
        padding: 0;
        margin: 0;
    }
    .sing-img{
        height: 45vh;
        width: 280px;
        margin-left: 8%;
        margin-top: 20%;
        border: none;
    }
    .single-picture {
        height: 58vh;
        width: 90%;
        margin-left: 10%;
        display: flex;
    }
    .single-det {
        width: 100%;
        margin-top: 90%;
    }
    .sin-heart {
        margin-top: 50%;
        margin-right: 30%;
    }
    .sin-heart:hover{
        color: rgb(250, 89, 89);
        transition: 0.5s;
    }
    .sin-picture {
        width: 70%;
    }
    .cart-buy-div{
        display: grid;
    }
    .si-butt-1{
        padding-top: 2%;
        width: 125%;
        margin-left: 4%;
        margin-top: -10%;
        border-radius: 15px;
    }
    .si-butt-2{
        padding-top: 3.5%;
        width: 125%;
        margin-left: 4%;
        margin-top: -26%;
        border-radius: 15px;
    }
    .single-rate{
        height: 20px;
        width: 40px;
    }
    .single-report{
        margin-top: 5vh;
        margin-left: 40%;
    }

    .sugg-full-slider-div{
        height: 28vh;
    }
    .single-Slider_div2 {
        height: 22vh; 
        width: 98%; 
        margin-left: 1%; 
        margin-right: 1%; 
    }
    .single-product-pic img {
        margin-top: 1.5vh; 
        height: 20vh; 
        width: 150%; 
    }
    .mySwiper{
        width: 98%;
    }
    
  }
 






  @media screen and (max-width: 380px) {
    .single-inner-upper {
        flex-direction: column;
        height: 165vh;
    }
    .single-inner {
        width: 95%;
        padding-top: 25%;
        height: 220vh; 
        margin-left: 2.5%;
    }
    .single-pic {
        height: 18vh;
        width: 75%;
        padding: 0;
        margin: 0;
    }
    .sing-img {
        height: 50vh;
        width: 260px;
        margin-left: 10%;
        margin-top: 25%;
        border: none;
    }
    .single-picture {
        height: 60vh;
        width: 95%;
        margin-left: 5%;
        display: flex;
    }
    .single-det {
        width: 100%;
        margin-top: 110%;
    }
    .sin-heart {
        margin-top: 55%;
        margin-right: 25%;
    }
    .sin-heart:hover {
        color: rgb(250, 89, 89);
        transition: 0.5s;
    }
    .sin-picture {
        width: 75%;
    }
    .cart-buy-div {
        display: grid;
    }
    .si-butt-1 {
        padding-top: 2%;
        width: 110%;
        margin-left: 5%;
        margin-top: -12%;
        border-radius: 15px;
    }
    .si-butt-2 {
        padding-top: 3.5%;
        width: 110%;
        margin-left: 5%;
        margin-top: -28%;
        border-radius: 15px;
    }
    .single-rate {
        height: 18px;
        width: 38px;
    }
    .single-report {
        margin-top: 3vh;
        margin-left: 35%;
    }
    .sugg-full-slider-div {
        height: 30vh;
    }
    .single-Slider_div2 {
        height: 25vh;
        width: 95%;
        margin-left: 2.5%;
        margin-right: 2.5%;
    }
    .single-product-pic img {
        margin-top: 2vh;
        height: 22vh;
        width: 140%;
    }
    .mySwiper {
        width: 95%;
    }
}
