*{
    margin: 0;
    padding: 0;
}
.main-outer{
    width: 100%;
    padding-top: 12vh;
    padding-bottom: 5vh;
}
.about-outer{
    height: 42.5vh;
    width: 100%;
    /* background-color: aqua; */
    background-image: url('../pictures/1st_banner.png');
    background-attachment: fixed;
    background-position:center;
    background-size:auto;
    background-repeat: no-repeat;
}
.title{
    height: 30vh;
    width: 100%;
    background-color: rgb(0, 0, 0 ,0.7);
    text-align: center;
    padding-top: 12.5vh;
    
}
.about{
    font-size: 80px;
    text-align: center;
    color: white;
}
.body{
    height: 70vh;
    width: 100%;
    /* background-color: #ebeaeaf4; */
    display: inline-flex;
    margin-top: 3vh;
}
.about1{
    font-size: 52px;
    text-align: center;
    margin-top: 6vh;
}
.about-new-1{
    font-size: 52px;
    text-align: center;
    margin-top: 6vh;
}
.image{
    height: 70vh;
    width: 37%;
    margin-left:11% ;
    /* background-color: aqua; */
}
.about-text-1{
    height: 69vh;
    width: 35%;
    /* background-color: rgb(185, 255, 178); */
    margin-right: 10%;
    margin-left: 8%;
    line-height: 5vh;
}
.about-text{
    text-align:justify;
    font-size: 14px;

}
.about-text1{
    height: 30vh;
    width: 80%;
    margin-left: 10%;
    /* background-color: aqua; */
    line-height: 5vh;
}
.about-textbox{
    text-align:justify;
    font-size: 14px;

}
.new-promise{
    height: 8.5vh;
    width: 100%;
    background-color: rgb(240, 240, 240);
    padding-top: 5vh;
    box-shadow: 0px -10px 17px 2px rgba(105, 105, 105, 0.2); 
}
.promise{
    text-align: center;
    font-size: 37px;
    background-color: rgb(240, 240, 240);
}
.box{
    height: 45vh;
    width: 96%;
    background-color: rgb(240, 240, 240);
    display: inline-flex;
    padding-top: 5vh;
    padding-left: 4%;
    box-shadow: 0px 15px 15px -5px rgba(78, 78, 78, 0.2); 
}
.box1{
    height: 40vh;
    width: 27.5%;
    /* background-color: azure; */
    margin-left: 5%;
    margin-top: 2vh;
}
.box2{
    height: 40vh;
    width: 27.5%;
    /* background-color: azure; */
    margin-left: 5%;
    margin-top: 2vh;
}
.box3{
    height: 40vh;
    width: 27.5%;
    /* background-color: azure; */
    margin-left: 5%;
    margin-top: 2vh;
}
.text1{
    height: 5vh;
    width: 80%;
    /* background-color: rgb(172, 255, 177); */
    text-align: center;
}
.img1{
    height: 30vh;
    width: 100%;
    /* background-color: rgb(255, 172, 241); */
}
.our-promise{
    font-size: 20px;
    margin-top: 5vh;
}
.follow{
    height: 80vh;
    width: 100%;
    /* background-color: aquamarine; */
    display: inline-flex;
}
.follow-text{
    height: 70vh;
    width: 45%;
    margin-left: 13%;
    /* background-color: rgb(250, 210, 149); */
    margin-top: 6vh;
    line-height: 5vh;
    text-align: justify;
}
.follow-img{
    height: 80vh;
    width: 30%;
    /* background-color: rgb(255, 187, 232); */
    margin-left: 7%;
}
.about-text-last{
    text-align:justify;
    font-size: 14px;

}
.button{
    height: 7vh;
    width: 30%;
    background-color: rgb(23, 9, 64);
    color: white;
    text-align: center;
    margin-top: 10vh;
    margin-left: 35%;
    border-radius: 5px;
    padding-top: 1vh;
}
.follow-us{
    font-size: larger;
}
.image-new{
    box-shadow: 0px 15px 15px 5px rgba(78, 78, 78, 0.2); 
}


@media only screen and (max-width: 1290px) {
    .body {
        flex-direction: column;
    }
    .image{
        margin-left:17% ;
        margin-bottom: 2vh;
        text-align: center;
    }

    
    .about-text-1{
        width: 80%;
        height: 101vh;
        margin:0;
        padding-left: 10%;
        padding-right: 10%;
    }
    .about1{
        margin-top: 10vh;
    }
    .about-new-1{
        margin-top: 35vh;
    }
    .about-text{
        padding-bottom: 10vh;
        margin-top: 10vh;
    }
    .about-textbox{
        margin-top: 3vh;
    }
    .new-promise{
        /* height: 5vh;
        margin-top: 5vh; */
        display: none;
    }
    .box{
        /* width: 96%;
        padding-left: 2%;
        padding-right: 2%; */
        display: none;
    }

    .box1{
        width: 27%;
        margin-left: 4%;
        
    }
    .box2{
        width: 27%;
        margin-left: 5%;
        
    }
    .box3 {
        width: 27%;
        margin-left: 5%;
    }
    .text1 {
        width: 90%;
        margin: 5% auto;
        margin-left: 15%;
    }

    .button {
        margin-left: 35%;
        margin-top: 3vh;
        
    }
    .our-promise{
        padding-bottom: 60vh;
    }

    .follow-text {
        width: 80%;
        margin-left: 10%;
        padding-bottom: 10vh;
    }

    .follow-img {
        width: 50%;
        padding-right: 10%;
        margin-left: 5%;
        margin-top: 7vh;
    }
}


@media only screen and (max-width: 420px) {
    
    .about-outer{
        width: 100%;
        padding: 0;
    }
    .title{
        width: 100%;
    }
    .body {
        flex-direction: column;
    }
    .image {
        height: 50vh;
        width: 100%;
        margin-left:0% ;
    }
    .image img{
        width: 95%;
    }

    .about-text-1 {
        width: 86%;
        height: auto;
        margin: 0;
        padding: 7%;
    }
    .about1 {
        width: 75%;
        padding-left: 10%;
        margin-top: 28vh;
        text-align: center;
        margin-left: 1%;
    }
    .about-new-1 {
        width: 90%;
        padding-left: 5%;
        padding-right: 5%;
        margin-top: 80vh;
    }
    .about-text {
        padding-bottom: 5vh;
        margin-top: 5vh;
    }
    .about-textbox {
        margin-top: 3vh;
        width: 100%;
    }
    .new-promise,
    .box,
    .box1,
    .box2,
    .box3 {
        width: 90%;
        margin: 0 auto;
        margin-bottom: 5vh;
    }
    .text1 {
        width: 90%;
        margin: 5% auto;
        margin-left: 0;
    }
    .our-promise {
        padding-bottom: 30vh;
    }
    .follow{
        width:100% ;
        display: contents;
    }
    .follow-text {
        width: 86%;
        margin-left: 7%;
        padding-bottom: 10vh;
    }

    .follow-img {
        width: 80%;
        padding-right: 10%;
        margin-left: 5%;
        display: none;
    }
}