@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
.outer-div1{
    height: 29vh;
    width: 100%;
    background-color: rgb(211, 211, 211);
    padding-top: 1vh;
    font-family: 'Ubuntu', sans-serif;
}
.inner-2{
    height: 25vh;
    width: 98%;
    margin-left: 0.5%;
    background-color: white;
    display: inline-flex;
    padding-top: 3vh;
    padding-right: 1%;
    border-radius: 5px;
}
.product-outer{
    padding-top: 12vh;
}

@media screen and (max-width: 420px){
    .outer-div1{
        height: 9vh;
    }
    .inner-2{
        height: 8vh;
        margin-top: -5%;
    }
}